import { GiftColor, GiftColor1 } from './GiftColor'
import { isAnyMatch } from '../utils/commonUtils';
import { LabelStatus, LabelSkillStatus, LabelChip } from './Label';



function getCheckedTexts(dataEl, effectItems) {
  let checkedTexts = [];
  if (effectItems.length > 0) {
    if (dataEl.effects) {
      for (const effectEl of dataEl.effects) {
        if (isAnyMatch(effectItems, Object.keys(effectEl.effectItems))) {
          checkedTexts.push(effectEl.text);
        }
      }
    }
  }
  return checkedTexts;
}





const width = {
  status: 420,
  skill: 500,
  charAbility: 200,
  gift: 200,
}
const skillStatusMap = {
  damage: 'ダメージ',
  break: 'ブレイク',
  heal: '回復力',
  wait: 'ウェイト',
}
const skillCuurentStatusMap = {
  currentDamage: 'ダメージ',
  currentBreak: 'ブレイク',
  currentHeal: '回復力',
  wait: 'ウェイト',
}

const statusStyle = {
  boxSizing: 'border-box', /* borderとpaddingを含めたサイズ指定を有効にする */
  width: 60,
  height: '100%',
  position: 'relative',
  textAlign: 'right',
  border: '1px solid darkgray',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'flex-end',
}



const statusChartStyle = (value, category, key, isSortItem) => {

  return (
    {
      position: 'absolute',
      top: 0,
      left: 0,
      height: '100%',
      width: value ? (value / maxValues[category][key] * 58) : 0,
      backgroundColor: isSortItem ? darken(checkColor, 0.2) : lighten(mainBackgroundColor, 0.2)
    }
  )
}

const statusValueStyle = {
  color: mainColor,
  // width: 60,
  position: 'relative',
  right: '4px',

  display: 'flex',
  alignItems: 'center',
  height: '100%',
}


const skillStatus = ['damage', 'break', 'heal', 'wait']
// const currentSkillStatusKeys = ['currentDamage', 'currentBreak', 'currentHeal', 'wait']
const currentSkillStatusKeys = ['damage', 'break', 'heal', 'wait']//暫定


const skillAreaStyle = (isFilterItemArea) => {
  return {
    width: 20,
    minWidth: 20,
    height: 20,
    textAlign: 'center',
    backgroundColor: isFilterItemArea ? darken(checkColor, 0.2) : 'transparent',  // 'default'を'transparent'に変更しました
  };
}
const splitSortItem = (text) => {
  const result = text.match(/([a-zA-Z]+)(\d+)/);
  if (result) {
    const [, status, index] = result;
    return { status, index };
  }
  return { status: '', index: '' };
};

const SkillStatusView = ({ skillName, skillStatus, sortItem, skillIndex }) => {

  let { status, index } = splitSortItem(sortItem)
  if (skillIndex === undefined) {
    status = sortItem
  }

  return (
    <div style={{ display: 'flex' }}>
      {skillStatus.map(key => {

        let isSortItem = false
        if (key === status && (skillIndex === undefined || skillIndex === Number(index))) {
          isSortItem = true
        }

        return (
          <div key={key} style={{ ...statusStyle }}>
            {/* <div style={statusChartStyle(skill[skillName][key], maxValues[key], isSortItem)}></div> */}
            <div style={statusChartStyle(skill[skillName][key], skillIndex === undefined ? 'skill' : 'character', key, isSortItem)}></div>
            <div style={statusValueStyle}>
              {
                skill[skillName][key]
                  ? skill[skillName][key].toLocaleString() + (key !== 'wait' ? '%' : '')
                  : '-'
              }
            </div>
          </div>
        )
      })}
    </div>
  );
};


