import React, { useState, useEffect } from 'react';
import { Badge, Button, Collapse, Chip, Grid, List, ListItem, ListSubheader, Stack, Tooltip } from '@mui/material';
import { darken, lighten } from '@mui/system';

import gift from '../constants/gift';
import { Img } from './Img'
import { mainBackgroundColor, checkColor, mainColor, giftColorMap } from '../utils/commonStyle';

const statusKeys = ['HP', '素早さ', '物攻', '物防', '魔攻', '魔防']

const styleStatusKey = {
  boxSizing: 'border-box',
  display: 'flex',
  justifyContent: 'center',//横方向の中央寄せ
  alignItems: 'center',//縦方向の中央寄せ
  width: 60,
  height: 20,
  borderRadius: 10,
  backgroundColor: darken(mainBackgroundColor, 0.2),
  fontSize: '12px',
}

export const LabelStatus = () => {
  return (
    <div style={{ display: 'flex' }}>
      {statusKeys.map((key) => (
        <div key={key} style={styleStatusKey} >
          {key}
        </div>
      ))}
    </div>
  )
}

const skillStatusMap = {
  damage: 'ダメージ',
  break: 'ブレイク',
  heal: '回復力',
  wait: 'ウェイト',
}

export const LabelSkillStatus = () => {
  return (
    <div style={{
      display: 'flex',
      paddingTop: '4px'
    }}>
      <div style={{ width: 40 }}>
      </div>
      {Object.keys(skillStatusMap).map((key) => (
        <div key={key} style={styleStatusKey} >
          <Img name={key} square={18} borderRadius={4} />
        </div>
      ))}
    </div>
  )
}




const styleChip = (iconName, isChecked, isChecked1, isChecked2, isChecked3, width) => ({
  color: mainColor,
  width: width ?? null,
  height: 18,
  fontSize: '12px',
  margin: '1px',
  pl: iconName ? '1px' : 0,
  backgroundColor: (isChecked || isChecked1) ? darken(checkColor, 0.2) : isChecked2 ? darken(checkColor, 0.4) : isChecked3 ? darken(checkColor, 0.6) : undefined,
  justifyContent: 'flex-start',
})
export const LabelChip = ({ iconName, label, isChecked, isChecked1, isChecked2, isChecked3, width }) => {
  console.log()
  return (
    <Chip
      icon={iconName
        ? <Img name={iconName} square={16} borderRadius={8} />
        : <></>
      }
      label={label}
      size='small'
      sx={styleChip(iconName, isChecked, isChecked1, isChecked2, isChecked3, width)}
    />
  )
}